.spinner-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(120deg, #f8fafc 0%, #e0e7ef 100%);
  }
  
  .spinner {
    width: 64px;
    height: 64px;
    border: 7px solid #e0e7ef;
    border-top: 7px solid #1a73e8;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 24px;
    box-shadow: 0 4px 24px rgba(26,115,232,0.10);
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg);}
    100% { transform: rotate(360deg);}
  }
  
  .text-wrapper {
    margin-bottom: 20px;
    text-align: center;
    font-size: 1.3rem;
    color: #1a73e8;
    font-family: "Playfair Display", serif;
    letter-spacing: 1px;
  }
  
  .button-wrapper {
    margin-top: 10px;
  }
  
  .button-wrapper button {
    background: linear-gradient(90deg, #1db954 0%, #1a73e8 100%);
    color: #fff;
    border: none;
    border-radius: 6px;
    padding: 10px 28px;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.2s, transform 0.2s;
    box-shadow: 0 2px 8px rgba(29,185,84,0.08);
  }
  
  .button-wrapper button:hover {
    background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
    transform: scale(1.05);
  }