@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* ========== Navbar ========== */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 16px -6px rgba(60, 60, 60, 0.13);
  border-bottom: 2px solid #e0e7ef !important;
  background: linear-gradient(90deg, #f8fafc 0%, #e0e7ef 100%);
  position: sticky;
  top: 0;
  z-index: 100;
  transition: background 0.3s;
}

.nav-link {
  font-weight: 400 !important;
  color: #1a73e8 !important;
  letter-spacing: 1px;
  transition: color 0.2s;
}

.nav-link:hover,
.active {
  color: #1db954 !important;
  border-bottom: 2px solid #1db954;
  background: rgba(29, 185, 84, 0.07);
}

.navbar-brand {
  font-weight: 700;
  font-family: "Playfair Display", serif;
  letter-spacing: 3px;
  color: #1a73e8 !important;
  font-size: 1.7rem;
  transition: color 0.2s;
}

.navbar-brand:hover {
  color: #1db954 !important;
}

.search-form {
  margin-right: 10px;
  margin-top: 4px;
}
.search-form input {
  border: none;
  border-radius: 6px;
  padding: 7px 12px;
  background: #f8fafc;
  box-shadow: 0 1px 4px rgba(26, 115, 232, 0.03);
  font-size: 1rem;
}
.search-form input:focus {
  outline: 1.5px solid #1a73e8;
  background: #fff;
}
.search-form button {
  background: linear-gradient(90deg, #1db954 0%, #1a73e8 100%);
  border-radius: 6px;
  color: white;
  border: none;
  padding: 7px 18px;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
}
.search-form button:hover {
  background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
  transform: scale(1.05);
}

/* ========== Footer ========== */
.footer {
  color: white;
  padding: 28px 0 18px 0;
  background: linear-gradient(to right, #434343, #1a73e8, #1db954, #000000);
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 1.1rem;
  letter-spacing: 1px;
}

.footer a {
  text-decoration: none;
  color: #ffefba;
  padding: 10px;
  transition: color 0.2s, border-bottom 0.2s;
}

.footer a:hover {
  color: #1db954;
  border-bottom: 2px solid #1db954;
}

/* ========== Page Not Found ========== */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(120deg, #f8fafc 0%, #e0e7ef 100%);
  border-radius: 18px;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
  color: #1a73e8;
  letter-spacing: 2px;
}

.pnf-heading {
  font-weight: normal;
  color: #1db954;
  font-size: 2rem;
}

.pnf-btn {
  color: #fff;
  background: linear-gradient(90deg, #1db954 0%, #1a73e8 100%);
  border: none;
  border-radius: 8px;
  text-decoration: none;
  padding: 12px 32px;
  margin-top: 18px;
  font-weight: bold;
  font-size: 1.1rem;
  transition: background 0.2s, color 0.2s, transform 0.2s;
  box-shadow: 0 2px 8px rgba(29, 185, 84, 0.08);
  cursor: pointer;
}
.pnf-btn:hover {
  background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
  color: #fff;
  transform: scale(1.05);
}

/* ========== Contact Us ========== */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(120deg, #f8fafc 0%, #e0e7ef 100%);
  border-radius: 18px;
}

/* ========== Product Link ========== */
.product-link {
  text-decoration: none !important;
  color: #1a73e8 !important;
  font-weight: 500;
  transition: color 0.2s;
}
.product-link:hover {
  color: #1db954 !important;
}

/* ========== Category Button ========== */
.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
  background: linear-gradient(90deg, #1db954 0%, #1a73e8 100%);
  color: #fff;
  border: none;
  border-radius: 10px;
  margin-bottom: 18px;
  font-weight: bold;
  letter-spacing: 1.2px;
  transition: background 0.2s, color 0.2s, transform 0.2s;
  box-shadow: 0 2px 8px rgba(29, 185, 84, 0.08);
  cursor: pointer;
}
.cat-btn:hover {
  background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
  color: #fff;
  transform: scale(1.04);
}

/* ========== Dashboard ========== */
.dashboard {
  margin-top: 100px !important;
  background: linear-gradient(120deg, #f8fafc 0%, #e0e7ef 100%);
  border-radius: 18px;
  padding: 24px 0;
}

.dashboard-menu h4 {
  background: linear-gradient(90deg, #434343 0%, #1a73e8 100%);
  color: white !important;
  padding: 20px 0px;
  border-radius: 8px;
  font-family: "Playfair Display", serif;
  font-size: 1.3rem;
  letter-spacing: 1px;
  margin-bottom: 18px;
  text-align: center;
}

/* ========== Responsive ========== */
@media (max-width: 900px) {
  .navbar {
    font-size: 15px;
  }
  .footer {
    font-size: 1rem;
    padding: 18px 0 12px 0;
  }
  .dashboard {
    margin-top: 70px !important;
    padding: 12px 0;
  }
}

@media (max-width: 600px) {
  .navbar-brand {
    font-size: 1.1rem;
    letter-spacing: 1px;
  }
  .dashboard-menu h4 {
    font-size: 1.1rem;
    padding: 12px 0px;
  }
  .cat-btn {
    font-size: 1.1rem;
    padding: 18px 0px;
  }
  .pnf-title {
    font-size: 60px;
  }
  .pnf-heading {
    font-size: 1.2rem;
  }
}