.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  background: linear-gradient(120deg, #ffdee9 0%, #b5fffc 100%);
  animation: authBgColorChange 16s linear infinite;
  transition: background 0.5s;
  padding: 2.5rem 0;
  position: relative;
  overflow: hidden;
}

/* Animated background for auth page */
@keyframes authBgColorChange {
  0% {
    background: linear-gradient(120deg, #ffdee9 0%, #b5fffc 100%);
  }
  25% {
    background: linear-gradient(120deg, #ffe29f 0%, #ffa99f 100%);
  }
  50% {
    background: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  }
  75% {
    background: linear-gradient(120deg, #fbc2eb 0%, #a6c1ee 100%);
  }
  100% {
    background: linear-gradient(120deg, #ffdee9 0%, #b5fffc 100%);
  }
}

.form-container::before {
  content: "";
  position: absolute;
  left: -80px;
  top: -80px;
  width: 220px;
  height: 220px;
  background: radial-gradient(circle, #1a73e8 0%, transparent 70%);
  opacity: 0.08;
  z-index: 0;
  pointer-events: none;
}
.form-container::after {
  content: "";
  position: absolute;
  right: -100px;
  bottom: -100px;
  width: 260px;
  height: 260px;
  background: radial-gradient(circle, #1db954 0%, transparent 70%);
  opacity: 0.09;
  z-index: 0;
  pointer-events: none;
}

.form-container form {
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.18);
  padding: 38px 32px;
  background-color: #fff;
  border-radius: 22px;
  min-width: 320px;
  max-width: 390px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  animation: formSlideIn 1s cubic-bezier(0.68, -0.55, 0.27, 1.55);
  z-index: 1;
}

@keyframes formSlideIn {
  0% {
    transform: translateY(-60px) scale(0.95);
    opacity: 0;
  }
  100% {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

.form-container form .title {
  text-align: center;
  margin-bottom: 22px;
  font-weight: bold;
  font-family: 'Playfair Display', serif;
  font-size: 2rem;
  color: #1a73e8;
  letter-spacing: 1.5px;
  background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.form-container form input,
.form-container form select,
.form-container form textarea {
  width: 100%;
  padding: 16px 18px;           /* Increased padding for a larger writing area */
  margin-bottom: 18px;
  border: 1.5px solid #e0e7ef;
  border-radius: 8px;
  font-size: 1.15rem;           /* Slightly larger font for better readability */
  transition: border 0.2s, box-shadow 0.2s;
  background: #f8fafc;
  font-family: inherit;
  box-shadow: 0 1px 4px rgba(26,115,232,0.03);
  min-height: 48px;             /* Ensures a comfortable height for input fields */
  resize: vertical;             /* Allows textarea to be resized vertically */
}

/* For textarea specifically, make it taller by default */
.form-container form textarea {
  min-height: 90px;
  max-height: 220px;
}

.form-container form input:focus,
.form-container form select:focus,
.form-container form textarea:focus {
  border: 1.5px solid #1a73e8;
  outline: none;
  background: #fff;
  box-shadow: 0 2px 8px rgba(26,115,232,0.08);
}

.form-container form button {
  border: none;
  border-radius: 10px;
  background: linear-gradient(90deg, #1db954 0%, #1a73e8 100%);
  color: white;
  width: 100%;
  padding: 14px 0;
  font-size: 1.15rem;
  font-weight: bold;
  letter-spacing: 1.2px;
  margin-top: 10px;
  box-shadow: 0 2px 12px rgba(29, 185, 84, 0.10);
  cursor: pointer;
  transition: background 0.2s, transform 0.2s, box-shadow 0.2s;
}

.form-container form button:hover {
  background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
  transform: scale(1.045);
  box-shadow: 0 6px 24px rgba(26,115,232,0.13);
}

.form-container .switch-position {
  position: absolute;
  top: 16px;
  right: 18px;
  background: #f8fafc;
  border: 1px solid #e0e7ef;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(29, 185, 84, 0.08);
  z-index: 2;
}

.form-container .switch-position:hover {
  background: #e0e7ef;
  box-shadow: 0 4px 16px rgba(26,115,232,0.13);
}

@media (max-width: 600px) {
  .form-container {
    padding: 0.5rem 0;
  }
  .form-container form {
    min-width: unset;
    max-width: 98vw;
    padding: 18px 6px;
  }
  .form-container .switch-position {
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
  }
  .form-container form .title {
    font-size: 1.3rem;
    margin-bottom: 14px;
  }
}