/* Banner */
.banner-img {
  margin-top: 60px;
  width: 100%;
  height: 350px;
  object-fit: cover;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.12);
}

/* Headings */
.home-page h1 {
  color: #333;
  font-family: "Playfair Display", serif;
  font-size: 2.5rem;
  letter-spacing: 2px;
  margin-bottom: 30px;
  text-align: center;
}

/* Home Page Layout */
.homepage-main {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 2.5rem;
  background: linear-gradient(120deg, #f8fafc 0%, #e0e7ef 100%);
  min-height: 100vh;
  padding: 2rem 0;
  width: 100%;
  box-sizing: border-box;
}

/* Filters on the left */
.filters {
  flex: 0 0 260px;
  max-width: 260px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.07);
  padding: 2rem 1.5rem;
  margin-bottom: 2rem;
  margin-left: 1rem;
  position: sticky;
  top: 90px;
  height: fit-content;
}

/* Products on the right */
.home-page {
  flex: 1 1 0%;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: flex-start;
  padding-right: 2rem;
}

/* Card Styles */
.home-page .card {
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  width: calc((100% - 4rem) / 3); /* 3 cards per row, 2rem gap between */
  min-width: 270px;
  max-width: 340px;
  margin-bottom: 2rem;
  transition: transform 0.2s, box-shadow 0.2s;
  overflow: hidden;
  border: none;
  display: flex;
  flex-direction: column;
}

.home-page .card:hover {
  transform: translateY(-8px) scale(1.03);
  box-shadow: 0 8px 32px rgba(0,0,0,0.16);
}

.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.home-page .card-price {
  color: #1db954;
  font-weight: bold;
  font-size: 1.1rem;
}

.home-page button {
  border-radius: 0 0 8px 8px;
  width: 100%;
  background: linear-gradient(90deg, #1db954 0%, #1a73e8 100%);
  color: #fff;
  border: none;
  font-size: 15px;
  font-weight: bold;
  padding: 10px 0;
  transition: background 0.2s, color 0.2s;
  letter-spacing: 1px;
  margin-top: 10px;
}

.home-page button:hover {
  background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
  color: #fff;
}

.home-page .card-text {
  color: #5a5959;
  font-size: 0.97rem;
  margin-bottom: 10px;
}

.home-page .card img {
  height: 220px;
  object-fit: cover;
  border-top-left-radius: 14px;
  border-top-right-radius: 14px;
  transition: transform 0.3s;
  width: 100%;
}

.home-page .card img:hover {
  transform: scale(1.05);
}

.home-page .card-img-top {
  height: 220px !important;
}

/* ==================
   Filters
================== */
.filters h4 {
  margin-top: 40px;
  color: #1a73e8 !important;
  border-bottom: 2px solid #e0e7ef;
  padding-bottom: 8px;
  font-size: 1.3rem;
  letter-spacing: 1px;
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px 0;
  font-size: 1rem;
}

.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
  font-size: 1rem;
}

.filters button {
  background: linear-gradient(90deg, #1db954 0%, #1a73e8 100%);
  width: 100%;
  border: none;
  border-radius: 6px;
  margin-top: 20px;
  color: #fff;
  font-weight: bold;
  padding: 10px 0;
  font-size: 1rem;
  transition: background 0.2s;
}

.filters button:hover {
  background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
}

/* Load More */
.loadmore {
  color: #1db954;
  font-weight: bold;
  font-size: 22px !important;
  text-align: center;
  margin: 2rem 0;
  cursor: pointer;
  transition: color 0.2s;
}

.loadmore:hover {
  color: #1a73e8;
}

/* Responsive adjustments */
@media (max-width: 991px) {
  .homepage-main {
    flex-direction: column;
    align-items: stretch;
    gap: 0;
  }
  .filters {
    position: static;
    max-width: 100%;
    margin-left: 0;
    margin-bottom: 1.5rem;
  }
  .home-page {
    padding-right: 0;
    justify-content: center;
  }
}

/* Responsive: 2 cards per row on medium screens */
@media (max-width: 1200px) {
  .home-page .card {
    width: calc((100% - 2rem) / 2);
  }
}

/* Responsive: 1 card per row on small screens */
@media (max-width: 700px) {
  .home-page .card {
    width: 100%;
    min-width: unset;
    max-width: unset;
  }
}