.cart-page {
    margin-top: 64px;
    min-height: 100vh;
    animation: cartBgColorChange 16s linear infinite;
    background: linear-gradient(120deg, #f8fafc 0%, #e0e7ef 100%);
    padding-bottom: 40px;
  }
  
  /* Animated background for cart page */
  @keyframes cartBgColorChange {
    0% {
      background: linear-gradient(120deg, #f8fafc 0%, #e0e7ef 100%);
    }
    25% {
      background: linear-gradient(120deg, #ffe29f 0%, #ffa99f 100%);
    }
    50% {
      background: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
    }
    75% {
      background: linear-gradient(120deg, #fbc2eb 0%, #a6c1ee 100%);
    }
    100% {
      background: linear-gradient(120deg, #f8fafc 0%, #e0e7ef 100%);
    }
  }
  
  .cart-page h1 {
    padding: 18px 0 10px 0 !important;
    font-family: "Playfair Display", serif;
    font-weight: bold;
    background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
    color: #fff !important;
    border-radius: 12px;
    margin-bottom: 24px;
    box-shadow: 0 4px 24px rgba(0,0,0,0.10);
    text-align: center;
    letter-spacing: 2px;
  }
  
  .cart-page h1 > p {
    font-size: 20px;
    margin-top: 10px;
    color: #222;
  }
  
  .cart-page .card {
    padding: 18px 12px !important;
    min-height: 160px !important;
    margin-bottom: 18px !important;
    border-radius: 14px;
    box-shadow: 0 2px 16px rgba(0,0,0,0.08);
    border: none;
    background: #fff;
    display: flex;
    align-items: center;
    transition: transform 0.18s, box-shadow 0.18s;
  }
  
  .cart-page .card:hover {
    transform: translateY(-6px) scale(1.02);
    box-shadow: 0 8px 32px rgba(0,0,0,0.13);
  }
  
  .cart-page .cart-remove-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }
  
  .cart-page .cart-remove-btn button {
    background: linear-gradient(90deg, #ff5858 0%, #f09819 100%);
    color: #fff;
    border: none;
    border-radius: 6px;
    padding: 7px 18px;
    font-weight: bold;
    font-size: 1rem;
    transition: background 0.2s, transform 0.2s;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(255,88,88,0.08);
  }
  
  .cart-page .cart-remove-btn button:hover {
    background: linear-gradient(90deg, #f09819 0%, #ff5858 100%);
    transform: scale(1.07);
  }
  
  .cart-page .cart-summary {
    text-align: center;
    margin-top: 30px;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 2px 16px rgba(0,0,0,0.08);
    padding: 28px 18px 22px 18px;
    max-width: 420px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .cart-page .cart-summary h2 {
    color: #1a73e8;
    font-family: "Playfair Display", serif;
    font-size: 1.5rem;
    margin-bottom: 18px;
  }
  
  .cart-page .cart-summary p {
    color: #444;
    font-size: 1.1rem;
    margin-bottom: 10px;
  }
  
  .cart-page .cart-summary .checkout-btn {
    background: linear-gradient(90deg, #1db954 0%, #1a73e8 100%);
    color: #fff;
    border: none;
    border-radius: 8px;
    padding: 12px 0;
    width: 100%;
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 18px;
    transition: background 0.2s, transform 0.2s;
    box-shadow: 0 2px 8px rgba(29,185,84,0.08);
    cursor: pointer;
  }
  
  .cart-page .cart-summary .checkout-btn:hover {
    background: linear-gradient(90deg, #1a73e8 0%, #1db954 100%);
    transform: scale(1.04);
  }
  
  /* Responsive adjustments */
  @media (max-width: 800px) {
    .cart-page .card {
      flex-direction: column;
      min-height: unset !important;
      padding: 12px 6px !important;
    }
    .cart-page .cart-summary {
      padding: 18px 6px 14px 6px;
      max-width: 98vw;
    }
  }